import { Observable } from 'rxjs/internal/Observable';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ErrorHandlerService } from '../../services/error-handler.service';

import { catchError } from '../../../../node_modules/rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CategoryServicesService {

  constructor(private http:HttpClient,
  private errorHandler:ErrorHandlerService) { }

  url:string="https://www.blogsar.com/easy_mall_apis/createCategory.php";
  get_category_url:string="https://www.blogsar.com/easy_mall_apis/getCategories.php";
  deleteURL:string="https://www.blogsar.com/easy_mall_apis/deleteCategory.php";

  createCategory(data:create_Category):Observable<create_category_response>{

    return this.http.post<create_category_response>(this.url,data).pipe(
      catchError(this.errorHandler.handleError)
    )


  }


  getCategories():Observable<GetCategories>{

    return this.http.get<GetCategories>(this.get_category_url).pipe(
      catchError(this.errorHandler.handleError)
    )

  }

   id;

  deleteCategory(data:number):Observable<deleteCategoryResponse>{

    this.id={
      
      "category_id":data,
    }

    return this.http.post<deleteCategoryResponse>(this.deleteURL,this.id).pipe(
      catchError(this.errorHandler.handleError)
    )

  }
}

export interface create_Category{
  category_name:string;
}
export interface create_category_response{
  message:string;
  code:number;
}

export interface GetCategories{
  id:number;
  category:string;
}

export interface deleteCategoryResponse{
  message:string;
  code:number;
}