import { GetProductsForHomepageService, getProductsForHomepageResponse } from './get-products-for-homepage.service';
import { of, Observable } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LoadingStatusService {

  constructor(private homePage:GetProductsForHomepageService) { }



  //method for getting loading status
  isLoaded():Observable<boolean>{

    this.homePage.getProductsForHomepage().subscribe(
      (res:getProductsForHomepageResponse)=>{
        if(res){
          return of(false)
        }else{
          return of(true)
        }
      }
    )
    
return of(true);
  }
}

