import { CheckLoginUserService, loginResponsce } from './../../services/check-login-user.service';
import { CartServiceService, CartResponse } from './../../services/cart-service.service';
import { GetAllProductsService, getOneProductResponse } from './../../adminComponents/services/get-all-products.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '../../../../node_modules/@angular/router';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-explore-product',
  templateUrl: './explore-product.component.html',
  styleUrls: ['./explore-product.component.css']
})
export class ExploreProductComponent implements OnInit {

  constructor(private getProduct:GetAllProductsService,
  private route :ActivatedRoute,
private router :Router,
private cart_service:CartServiceService,
private isLoggedIn : CheckLoginUserService,
private snack_bar:MatSnackBar,
) { }

  productData:getOneProductResponse;
  ngOnInit() {

  this.route.snapshot.paramMap.get('product_id')

     this.getProduct.getProduct(this.route.snapshot.paramMap.get('product_id')).subscribe(
       (res4:getOneProductResponse)=>{
         if(res4){
          this.productData = res4;
         }else{
          this.router.navigate(['/']);
         }
        
       }
     )

  }
 
  loginId:number;
  //add to cart method
  addToCart(product_id,product_title){
     this.isLoggedIn.isLoggedIn().subscribe(
       (res:loginResponsce)=>{

         this.loginId = res.id

        this.cart_service.addToCart(res.id,product_id,product_title).subscribe(
          (res2:CartResponse)=>{
            this.snack_bar.open(res2.message,"OK",{
              duration:2000,
             
            });
           this.router.navigateByUrl('home/myCart')
       
          }
        )
       }


     )

     
    
  }

}
