import { ProfileInfoResponse } from './user-profile-info.service';
import { catchError } from 'rxjs/operators';
import { Observable } from 'rxjs/internal/Observable';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ErrorHandlerService } from './error-handler.service';

@Injectable({
  providedIn: 'root'
})
export class UserProfileInfoService {

  constructor(private http:HttpClient,
  private errorHandler:ErrorHandlerService) { }

   

  getProfileUrl:string ="https://www.blogsar.com/easy_mall_apis/getProfileInfo.php";
  updateUserInfoUrl:string ="https://www.blogsar.com/easy_mall_apis/UpdateProfile.php";


id;
  getProfileInfo(data):Observable<ProfileInfoResponse>{

    this.id = {
      "id":data
    }

    return this.http.post<ProfileInfoResponse>(this.getProfileUrl,this.id).pipe(
      catchError(this.errorHandler.handleError)
    )

  }

  //update the profile info
  updateProfileInfo(data:ProfileInfoResponse):Observable<UpdateProfileInfoResponse>{

    return this.http.post<UpdateProfileInfoResponse>(this.updateUserInfoUrl,data).pipe(
      catchError(this.errorHandler.handleError)
    )

  }
}


export interface ProfileInfoResponse{
  Name:string;
  email:string;
  mobile_number:string;
  address1:string;
  address2:string;

}

export interface UpdateProfileInfoResponse{
message:string;
code:number;

}
