import { categoryItemResponse, CategoryServiceService } from './../../services/category-service.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '../../../../node_modules/@angular/router';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit{

  constructor(
    private get_produc:CategoryServiceService ,
  private route :ActivatedRoute,
private router :Router) { }
    
    getAllProductsByCategory:categoryItemResponse;

    ngOnInit() {
      this.route.params.subscribe(queryParams => {

        this.get_produc.getItemByyCategory(queryParams.category_id).subscribe(
          (res:categoryItemResponse)=>{
           
            
              this.getAllProductsByCategory=res
            
           
            
          }
        )
      });
 
    
    


  }

}
