import { CheckLoginUserService, loginResponsce } from './../../services/check-login-user.service';
import { CartServiceService, getAdressResponse, placeOrderResponse } from './../../services/cart-service.service';
import { GetBuyNowResponse, BuyNowServiceService } from './../../services/buy-now-service.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '../../../../node_modules/@angular/router';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-buy-now',
  templateUrl: './buy-now.component.html',
  styleUrls: ['./buy-now.component.css']
})
export class BuyNowComponent implements OnInit {

  constructor(private route :ActivatedRoute,
  private getBuyNow:BuyNowServiceService,
private cart_items:CartServiceService,
private isLoggedIn :CheckLoginUserService,
private snack_bar : MatSnackBar,
private router :Router
) { }

  productData:GetBuyNowResponse;
  AllAddress :getAdressResponse;
  selectedAdress;
  selectedQuantity = 1;
  totalPrice:number = 0;
 



  ngOnInit() {

     this.getBuyNow.getBuyNowItem(this.route.snapshot.paramMap.get('product_id')).subscribe(
       (res:GetBuyNowResponse)=>{

        if(res){

         this.productData = res;
         console.log(res)

          this.isLoggedIn.isLoggedIn().subscribe(
            (res2:loginResponsce)=>{

              this.cart_items.getAdress(res2.id).subscribe(
                (res3:getAdressResponse)=>{
                  this.AllAddress = res3
                  
                }
              )
            }
          )
        }else{
          this.snack_bar.open("Product Not Found","OK",{
            duration:2000,
          });
          this.router.navigate(['/']);
        }
       }

      
     )

  }

  //place order
  placeOrder(product_id,address,quantity){
 
    if(!address || address =="First Adress is empty" || address == "First Adress is empty"){
      this.snack_bar.open("Please select Address","OK",{
        duration:2000,
      });
      return false;
    }
    this.isLoggedIn.isLoggedIn().subscribe(

      (res2:loginResponsce)=>{

   this.getBuyNow.placeBuyNowOrders(product_id,address,quantity,res2.id).subscribe(

     (res:placeOrderResponse)=>{

      this.snack_bar.open(res.message,"OK",{

        duration:2000,
      });
  
      this.router.navigate(['../../myOrders'])
     }
   )
  })
  }

}
