import { Router } from '@angular/router';
import { loginResponsce, CheckLoginUserService } from './../../services/check-login-user.service';
import { CartServiceService, getCartItemResponse, updateItemCountResponse, deleteCartItemResponse, getAdressResponse, placeOrderResponse } from './../../services/cart-service.service';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-my-cart',
  templateUrl: './my-cart.component.html',
  styleUrls: ['./my-cart.component.css']
})
export class MyCartComponent implements OnInit {

  constructor(private cart_items:CartServiceService,
  private isLoggedIn:CheckLoginUserService,
private snack_bar:MatSnackBar,
private router:Router) { }


 cartData:getCartItemResponse[];
 numbeOfItem:number;
 totalPrice:number = 0;
totalPayable:number;
deliveryFee:number;
AllAddress:getAdressResponse;
selectedAdress;



  ngOnInit() {
    this.isLoggedIn.isLoggedIn().subscribe(
      (res:loginResponsce)=>{
this.cart_items.getCartItem(res.id).subscribe(
  (res2:getCartItemResponse[])=>{
  
      this.cartData = res2;
      this.numbeOfItem =   this.cartData.length

      for(let i = 0; i < this.cartData.length ; i++){
        this.totalPrice += this.cartData[i].product_price*this.cartData[i].quantity;
      }

      this.totalPayable = this.cartData.length*20 + this.totalPrice;
      this.deliveryFee = this.cartData.length*20;
      
    
    
  }
)

this.cart_items.getAdress(res.id).subscribe(
  (res3:getAdressResponse)=>{
    this.AllAddress = res3
    
  }
)
  })

  

}

//update item count whenn increase quantityy in cart
updateItemCount(q,id){
  
  this.totalPrice = 0;
 this.totalPayable =0;
 this.deliveryFee =0;
console.log(q,id);
  this.cart_items.updateCartItemCount(q,id).subscribe(
    (res:updateItemCountResponse)=>{
      this.snack_bar.open(res.message,"OK",{
        duration:2000,
      });

      this.isLoggedIn.isLoggedIn().subscribe(
        (res:loginResponsce)=>{
  this.cart_items.getCartItem(res.id).subscribe(
    (res2:getCartItemResponse[])=>{
      this.cartData = res2;
  
      for(let i = 0; i < this.cartData.length ; i++){
        this.totalPrice += this.cartData[i].product_price*this.cartData[i].quantity;
      }

      this.totalPayable = this.cartData.length*20 + this.totalPrice;
      this.deliveryFee = this.cartData.length*20;
      
    }
  )
    })
    }
  )

}

//remove an item from cart
removeItem(id){
 
  this.totalPrice = 0;
 this.totalPayable =0;
 this.deliveryFee =0;
  this.cart_items.deleteCartItem(id).subscribe(

    (res:deleteCartItemResponse)=>{
      this.snack_bar.open(res.message,"OK",{
        duration:2000,
      });

      this.isLoggedIn.isLoggedIn().subscribe(
        (res:loginResponsce)=>{
  this.cart_items.getCartItem(res.id).subscribe(
    (res2:getCartItemResponse[])=>{
      this.cartData = res2;
      this.numbeOfItem =   this.cartData.length
      for(let i = 0; i < this.cartData.length ; i++){
        this.totalPrice += this.cartData[i].product_price*this.cartData[i].quantity;
      }

      this.totalPayable = (this.cartData.length)*20 + this.totalPrice;
      this.deliveryFee = this.cartData.length*20;
    }
  )
    })
    }

    
  )

}


//place the order
data;
placeOrder(user_id,address){

  if(!address || address =="First Adress is empty" || address == "First Adress is empty"){
    this.snack_bar.open("Please provide an address","OK",{
      duration:2000,
    });
    return false;
  }

 this.cart_items.placeCartOrders(user_id,address).subscribe(
   (res:placeOrderResponse)=>{
    this.snack_bar.open(res.message,"OK",{
      duration:2000,
    });

    this.router.navigate(['../myOrders'])
   }
 )
   
}


}