import { CategoryComponent } from './components/category/category.component';
import { OrderedProductsComponent } from './adminComponents/ordered-products/ordered-products.component';
import { BuyNowComponent } from './components/buy-now/buy-now.component';
import { MyOrdersComponent } from './components/my-orders/my-orders.component';
import { MyCartComponent } from './components/my-cart/my-cart.component';
import { ExploreProductComponent } from './components/explore-product/explore-product.component';
import { ControlCoroselComponent } from './adminComponents/control-corosel/control-corosel.component';
import { UpdateProductComponent } from './adminComponents/update-product/update-product.component';
import { CreateCategoryModelComponent } from './adminComponents/create-category-model/create-category-model.component';
import { CreateNewProductComponent } from './adminComponents/create-new-product/create-new-product.component';
import { AllProductsComponent } from './adminComponents/all-products/all-products.component';
import { AdminLoginComponent } from './adminComponents/admin-login/admin-login.component';
import { AdminGuardService } from './auth/admin-guard.service';
import { AdminSignupComponent } from './adminComponents/admin-signup/admin-signup.component';
import { HomePageComponent } from './components/home-page/home-page.component';
import { SignupGuardService } from './auth/signup-guard.service';
import { MyAccountComponent } from './components/my-account/my-account.component';
import { AdminHomeComponent } from './adminComponents/admin-home/admin-home.component';
import { LoginComponent } from './components/login/login.component';

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { SignupComponent } from './components/signup/signup.component';


const routes: Routes = [

  {path: 'signup', component:SignupComponent},
  {path: 'login', component:LoginComponent},
  {path :'admin-home',component:AdminHomeComponent, canActivate:[AdminGuardService],
    children:[
      {path:'all-product',component:AllProductsComponent},
      {path:'create-new-product',component:CreateNewProductComponent },
    
      {path:'update_product/:product_id',component:UpdateProductComponent},
      {path:'ordered-products',component:OrderedProductsComponent},
     
      {path:'control-corosel',component:ControlCoroselComponent},
      {path:'',       component:AllProductsComponent},
      {path: '**' ,redirectTo:'/admin-home',pathMatch:"full"}
    ]
},
  {path:'admin-signup', component:AdminSignupComponent, canActivate:[AdminGuardService]},
  {path:'admin-login', component:AdminLoginComponent},
  {path:'home', component:HomeComponent,
children :[
  {path:'explore-product/:product_id',component:ExploreProductComponent},
  {path:'category/:category_id',component:CategoryComponent},
  {path:'myAccount/:id',component:MyAccountComponent,canActivate:[SignupGuardService]},
  {path:'myCart',component: MyCartComponent,canActivate:[SignupGuardService]},
  {path:'myOrders',component: MyOrdersComponent,canActivate:[SignupGuardService]},
  {path:'buy-now/:product_id',component: BuyNowComponent,canActivate:[SignupGuardService]},

  {path:'',component: HomePageComponent}
]},
  {path:'',redirectTo:'/home',pathMatch:"full"},
  {path: '**' ,redirectTo:'/home', pathMatch:"full"}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
