import { catchError } from 'rxjs/operators';
import { ErrorHandlerService } from './error-handler.service';
import { CookieService } from 'ngx-cookie-service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

//this service returnes login status(i.e. true if logged in and false if not logged in)
//and the user id
export class CheckLoginUserService {

  constructor(private http: HttpClient,
    private cookieService: CookieService,
     private errorHandler : ErrorHandlerService) { }

  url :string = "https://www.blogsar.com/easy_mall_apis/checkUserLogin.php";

  data = {

    "token" : this.cookieService.get('SSID')

  }
  

  isLoggedIn(): Observable<loginResponsce>{

    return this.http.post<loginResponsce>(this.url,this.data).pipe(
      catchError(this.errorHandler.handleError)
    );
  }
}

export interface loginResponsce{
  id: number;
  expression : boolean;
  name:string;
}
