import { allProducts, GetAllProductsService, deleteResponse } from './../services/get-all-products.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '../../../../node_modules/@angular/material/paginator';
import { MatSort } from '../../../../node_modules/@angular/material/sort';
import { MatTableDataSource, MatTable } from '../../../../node_modules/@angular/material/table';
import { trigger, state, style, transition, animate } from '../../../../node_modules/@angular/animations';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';


@Component({
  selector: 'app-all-products',
  templateUrl: './all-products.component.html',
  styleUrls: ['./all-products.component.css'],
  animations:[
    trigger('detailExpand', [
      state('collapsed', style({height:'0px',minHeight:'0px'})),
      state('expanded',style({height:'*'})),
      transition('expanded<=>collapsed',animate('225ms cubic-bezier(0.4,0.0,0.2,1)')),


    ])
  ]
})
export class AllProductsComponent implements OnInit{
  

  constructor(private get_all_products:GetAllProductsService,
  private snack_bar:MatSnackBar) { }


Data:allProducts[];
isLoadingResults=true;
  
  displayedColumns:string[] = ['product_title','product_price','product_ratings','product_quantity','product_sales','product_badge','product_category'];

 

  @ViewChild(MatPaginator,{static:true})paginator : MatPaginator;
  @ViewChild(MatSort,{static:true})sort : MatSort;
  @ViewChild(MatTable,{static:true})table:MatTable<allProducts>;
  dataSource:MatTableDataSource<allProducts>;

  ngOnInit(){

    this.get_all_products.getAllProducts().subscribe(
      (res:allProducts[]) => {
      this.dataSource = new MatTableDataSource<allProducts>(res);
        
      this.dataSource.paginator=this.paginator;
      this.dataSource.sort = this.sort;
      this.isLoadingResults=false;
      }
    )
 
   
  
  }

deleteProduct(id:number){
  this.isLoadingResults=true;
this.table.renderRows();
  this.get_all_products.deleteProduct(id).subscribe(
    (res:deleteResponse)=>{

      this.snack_bar.open(res.message,"OK",{
        duration:2000,
      });
      
      if(res.code == 1){

        this.get_all_products.getAllProducts().subscribe(
          (res:allProducts[]) => {
          this.dataSource = new MatTableDataSource<allProducts>(res);
            
          this.dataSource.paginator=this.paginator;
          this.dataSource.sort = this.sort;
          this.isLoadingResults=false;

          }
        )
   
      }
    }
  )

}
  applyFilter(filterValue:string){
this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  

}

