import { Observable } from 'rxjs/internal/Observable';
import { catchError } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ErrorHandlerService } from '../../services/error-handler.service';


@Injectable({
  providedIn: 'root'
})
export class OrderedProductServicesService {

  constructor(private http: HttpClient,
  private errorHandler : ErrorHandlerService) { }

  getOrderedProductsUrl:string = "https://www.blogsar.com/easy_mall_apis/getOrderedProducts.php";
  getBillingUrl:string = "https://www.blogsar.com/easy_mall_apis/getBillingInfo.php";


  //method for getting ordered products

  getOrderedProducts():Observable<OrderedProductsResponse[]>{

    return this.http.get<OrderedProductsResponse[]>(this.getOrderedProductsUrl).pipe(
      catchError(this.errorHandler.handleError)
    )

  }


  //getBilling Info for each order
  data;
  getBillingInfo(id,time):Observable<BillingInfoResponse[]>{

    this.data = {
      'user_id':id,
      'ordered_at':time,
    }
   return this.http.post<BillingInfoResponse[]>(this.getBillingUrl,this.data).pipe(
     catchError(this.errorHandler.handleError)
   )
  }
}



export interface OrderedProductsResponse{

Name:string;
ordered_at:string;
user_id:number;
status:string;
address:string;




}

export interface BillingInfoResponse{
  product_id:number,
  title:string,
  quantity:number,
  price:number,
  
}