import { placeOrderResponse } from './cart-service.service';
import { Observable } from 'rxjs/internal/Observable';
import { catchError } from 'rxjs/operators';
import { ErrorHandlerService } from './error-handler.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class BuyNowServiceService {

  constructor(private http:HttpClient,
  private errorHandler : ErrorHandlerService) { }


  buyNowUrl:string = "https://www.blogsar.com/easy_mall_apis/getBuyNowProduct.php";
  placeOrderbuyNowUrl:string = "https://www.blogsar.com/easy_mall_apis/placeBuyNowOrder.php";


  //this method would return product that is being bought now
  id;
  getBuyNowItem(data):Observable<GetBuyNowResponse>{
 this.id = {
   'id':data
 }

 return this.http.post<GetBuyNowResponse>(this.buyNowUrl,this.id).pipe(
   catchError(this.errorHandler.handleError)
 )
  }

  //method for placing order
  data;

  placeBuyNowOrders(product_id,address,quantity,user_id):Observable<placeOrderResponse>{
    this.data={
      'product_id':product_id,
      'address':address,
      'quantity':quantity,
      'user_id':user_id
    }

    return this.http.post<placeOrderResponse>(this.placeOrderbuyNowUrl,this.data).pipe(
      catchError(this.errorHandler.handleError)
    )

  }
}


export interface GetBuyNowResponse{
  product_id:number;
  product_title:string;
  product_image:string;
  product_price:number;
  product_details:string;
  product_ratings:number;
  product_quantity:number;
  product_sales:number;
  product_badge:string;
  product_category:string;
}