import { ErrorHandlerService } from './error-handler.service';
import { getResponse } from './sign-up.service';
import { HttpClient ,HttpErrorResponse, HttpHeaders} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import {catchError, map} from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class SignUpService {

  constructor(private http: HttpClient,
    private errorHandler : ErrorHandlerService) { }

  //api url
  signUpUrl :string = "https://www.blogsar.com/easy_mall_apis/signUp.php";

  //headers currently not in use
  httpOptions = {
 headers : new HttpHeaders({
'content-type' : 'application/json',
                          })
                }

   //method for signing up
  signup(user:User) :Observable<getResponse>{

    return this.http.post<getResponse>(this.signUpUrl,user,this.httpOptions).pipe(
      catchError(this.errorHandler.handleError)
    );
  }




  }


  //interface for user data that we are sending to server
  export interface User{
name :string;
email:string;
password:string;
  }


  //interface of responce we are getting from server
  export interface getResponse{
    message:string;
    code:number;
    token:string;
  }

