import { CookieService } from 'ngx-cookie-service';

import { SignUpService, getResponse, User } from '../../services/sign-up.service';
import { Component, OnInit } from '@angular/core';
import {FormGroup,FormControl, Validators} from '@angular/forms';
import {  Router } from '@angular/router';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';



@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  _message: getResponse ;
  
 

  constructor(private signup:SignUpService,
     private route :Router,
    private cookieService: CookieService,
  private snack_bar:MatSnackBar) { }

  signUpForm = new FormGroup({
    name : new FormControl('',[Validators.required]),
    email : new FormControl('',[Validators.required, Validators.email]),
    password : new FormControl('',[Validators.minLength(6),Validators.required])
  });

  hide:boolean = true;
  ngOnInit() {

  }

  get name(){
    return this.signUpForm.get('name');
  }
  get email(){
    return this.signUpForm.get('email');
  }
  get password(){
    return this.signUpForm.get('password');
  }


  onSubmit(){
    
    this.signup.signup(this.signUpForm.value).
    subscribe((data : getResponse) => {this._message = data
      this.snack_bar.open(data.message,"OK",{
        duration:2000,
      });
    }
  ).add(()=>{
    if(this._message.code == 0){
      
     this.cookieService.set('SSID',this._message.token);
     window.location.href="/";

  }});

   
  }




}
