import { catchError } from 'rxjs/operators';
import { ErrorHandlerService } from './error-handler.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';

@Injectable({
  providedIn: 'root'
})
export class LoginUserService {

  constructor(private http: HttpClient,
    private errorHandler : ErrorHandlerService) { }

    //api url
  loginUrl :string = "https://www.blogsar.com/easy_mall_apis/loginUser.php";

  //headers currently not in use
  httpOptions = {
 headers : new HttpHeaders({
'content-type' : 'application/json',
                          })
                }

   //method for signing up
  loginUser(user:loginCredential) :Observable<loginResponse>{

    return this.http.post<loginResponse>(this.loginUrl,user,this.httpOptions).pipe(
      catchError(this.errorHandler.handleError)
    );
  }

}



 //interface for login data that we are sending to server
 export interface loginCredential{
  email:string;
  password:string;
  login_token:string;
    }
  
  
    //interface of responce we are getting from server
    export interface loginResponse{
      message:string;
      code:number;
      token:string;
    }
  