import { Observable } from 'rxjs/internal/Observable';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ErrorHandlerService } from './error-handler.service';
import { catchError } from '../../../node_modules/rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CartServiceService {

  constructor(private http:HttpClient,
  private errorHandler:ErrorHandlerService) { }
 
   addToCartUrl="https://www.blogsar.com/easy_mall_apis/addToCart.php";
   getCartItemsUrl:string="https://www.blogsar.com/easy_mall_apis/getCartItems.php"
   updateCartItemsCountUrl:string="https://www.blogsar.com/easy_mall_apis/updateCartItemCount.php"
   removeCartItemUrl:string="https://www.blogsar.com/easy_mall_apis/deleteCartItem.php"
   getAddressUrl:string="https://www.blogsar.com/easy_mall_apis/getAdress.php"
   placeCartOrderUrl:string="https://www.blogsar.com/easy_mall_apis/placeCartOrders.php"

data;
  addToCart(user_id:number,product_id:number,title:string):Observable<CartResponse>{

     this.data = {
       'user_id': user_id,
       'product_id':product_id,
       'title':title,
       'quantity':1,
     }
    
       return this.http.post<CartResponse>(this.addToCartUrl,this.data).pipe(
         catchError(this.errorHandler.handleError)
       )
  }

  //get cart item method
  userid;
  getCartItem(data):Observable<getCartItemResponse[]>{
this.userid = {
  'user_id':data
}

   return this.http.post<getCartItemResponse[]>(this.getCartItemsUrl,this.userid).pipe(
     catchError(this.errorHandler.handleError)
   )


  }

  //updating cart item count
  dataa;
  updateCartItemCount(val,id){
    this.dataa={
      'val':val,
      'id':id,
    }

    return this.http.post<updateItemCountResponse>(this.updateCartItemsCountUrl,this.dataa).pipe(
      catchError(this.errorHandler.handleError)
    )

  }

  //method for deleting cart item
  id;
  deleteCartItem(id):Observable<deleteCartItemResponse>{
  this.id ={
    'id':id
  }
    return this.http.post<deleteCartItemResponse>(this.removeCartItemUrl,this.id).pipe(
      catchError(this.errorHandler.handleError)
    )
  }

  //get all address of users
  user_id
  getAdress(id):Observable<getAdressResponse>{

    this.user_id={
      'user_id':id
    }

      return this.http.post<getAdressResponse>(this.getAddressUrl,this.user_id).pipe(
        catchError(this.errorHandler.handleError)
      )
  }


  //place orders of cart items
  dataaa;
  placeCartOrders(user_id,address):Observable<placeOrderResponse>{

     this.dataaa = {
       'user_id':user_id,
       'address':address,
     }

     return this.http.post<placeOrderResponse>(this.placeCartOrderUrl,this.dataaa).pipe(
       catchError(this.errorHandler.handleError)
     )
  }

}

export interface addToCart{
  user_id:number;
  product_id:number;
  title:string;
  quantity:number;
}

export interface CartResponse{
  message:string;
  code:number;
}
export interface placeOrderResponse{
  message:string;
  code:number;
}

export interface getCartItemResponse{
  user_id:number;
  product_id:number;
  title:string;
  quantity:number;
  product_image:string;
  product_price:number;
  id:number;
}

export interface updateItemCountResponse{
  message :string;
  code:number;
}
export interface deleteCartItemResponse{
  message:string;
  code:number;
}

export interface getAdressResponse{
  address1:string;
  adress2:string;
}