import { AdminSignupServiceService } from '../services/admin-signup-service.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CookieService } from 'ngx-cookie-service';
import { getResponse } from '../../services/sign-up.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-admin-signup',
  templateUrl: './admin-signup.component.html',
  styleUrls: ['./admin-signup.component.css']
})
export class AdminSignupComponent implements OnInit {


  //constructor 
  constructor(private adminSignup : AdminSignupServiceService,
    private route :Router,
   private cookieService: CookieService,
  private snack_bar:MatSnackBar) { }

  _message: getResponse ;
  
 

 

  adminSignUpForm = new FormGroup({
    name : new FormControl('',[Validators.required]),
    email : new FormControl('',[Validators.required, Validators.email]),
    password : new FormControl('',[Validators.minLength(6),Validators.required])
  });

  hide:boolean = true;
  ngOnInit() {

  }

  get name(){
    return this.adminSignUpForm.get('name');
  }
  get email(){
    return this.adminSignUpForm.get('email');
  }
  get password(){
    return this.adminSignUpForm.get('password');
  }


  onSubmit(){
    console.log("form is submitting");
    this.adminSignup.adminSignup(this.adminSignUpForm.value).
    subscribe((data : getResponse) => {
      this._message = data;
      this.snack_bar.open(data.message,"OK",{
      duration:2000,
    });
  }
  ).add(()=>{
    if(this._message.code == 0){
      
     this.cookieService.set('ASID',this._message.token);
     window.location.href="/admin-home";

  }});

   
  }


}
