import { Observable } from 'rxjs/internal/Observable';
import { catchError } from 'rxjs/operators';
import { ErrorHandlerService } from './../../services/error-handler.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AdminLoginService {
  constructor(private http: HttpClient,
    private errorHandler : ErrorHandlerService) { }

      //api url
  loginUrl :string = "https://www.blogsar.com/easy_mall_apis/loginAdmin.php";

  //headers currently not in use
  httpOptions = {
 headers : new HttpHeaders({
'content-type' : 'application/json',
                          })
                }

   //method for signing up
  loginUser(user:adminLoginCredential) :Observable<AdminLoginResponse>{

    return this.http.post<AdminLoginResponse>(this.loginUrl,user,this.httpOptions).pipe(
      catchError(this.errorHandler.handleError)
    );
  }

}


 //interface for login data that we are sending to server
 export interface adminLoginCredential{
  email:string;
  password:string;
  login_token:string;
    }
  
  
    //interface of responce we are getting from server
    export interface AdminLoginResponse{
      message:string;
      code:number;
      token:string;
    }
  