import { AdminLoginService, AdminLoginResponse } from './../services/admin-login.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.css']
})
export class AdminLoginComponent implements OnInit {

  constructor(private login :AdminLoginService,
    private cookieService : CookieService,
   private route : Router,
  private snack_bar:MatSnackBar ) { }

   //hide variable
  hide:boolean = true;
 
  //message from login api
  _message: AdminLoginResponse ;

  //initialize reactive form form for login
  adminLoginForm = new FormGroup({
    email : new FormControl('',[Validators.required, Validators.email]),
    password : new FormControl('',[Validators.minLength(6),Validators.required]),
    login_token : new FormControl(this.cookieService.get('ASID'))
                             });


    //getters for email and password
    get email(){
      return this.adminLoginForm.get('email');
    }
    get password(){
      return this.adminLoginForm.get('password');
    }

    //implement onSubmit method
    onSubmit(){
      console.log("form is submitting");
      this.login.loginUser(this.adminLoginForm.value).
      subscribe((data : AdminLoginResponse ) => { 
        this._message = data;
        this.snack_bar.open(data.message,"OK",{
        duration:2000,
      });
    }
    ).add(()=>{
      if(this._message.code == 0){
        
       this.cookieService.set('ASID',this._message.token);
       window.location.href="/"; 
  
    }else if(this._message.code == 4){
      window.location.href="/"; 
    }
  });
  
     
    }


  ngOnInit() {
  }

}