import { CategoryServicesService, create_category_response } from './../services/category-services.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-create-category-model',
  templateUrl: './create-category-model.component.html',
  styleUrls: ['./create-category-model.component.css']
})
export class CreateCategoryModelComponent {

  constructor(private category:CategoryServicesService,
  private snack_bar:MatSnackBar) { }

  createCategoryForm = new FormGroup({
    category_name: new FormControl('',[Validators.required])
   
  });

  get category_name(){
    return this.createCategoryForm.get('category_name');
  }

  onSubmit(){

    this.category.createCategory(this.createCategoryForm.value).subscribe((res:create_category_response)=>{

      this.snack_bar.open(res.message,"OK",{
        duration:2000
    })
   

  }) 

}


}
