import { CategoryServicesService, GetCategories } from './../services/category-services.service';
import { CreateProductService, productResponse } from './../services/create-product.service';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-create-new-product',
  templateUrl: './create-new-product.component.html',
  styleUrls: ['./create-new-product.component.css']
})
export class CreateNewProductComponent implements OnInit {

  constructor(private craeteproduct :CreateProductService,
  private snack_bar : MatSnackBar,

       private categoryy :CategoryServicesService) { }

  ngOnInit() {
  }

  _message;
  title:string;
  image_url:string;
  price:number;
  details:string;
  quantity:number;
  Category:string;

  category:GetCategories;
  
  createProductForm = new FormGroup({
    product_title : new FormControl('',[Validators.required,Validators.minLength(6)]),
    product_image : new FormControl('',[Validators.required]),
    product_price : new FormControl('',[Validators.required]),
    product_details : new FormControl('',[Validators.required]),
    product_quantity : new FormControl('',[Validators.required]),
    product_category : new FormControl('',[Validators.required])
  });

  hide:boolean = true;
  

  get product_title(){
    return this.createProductForm.get('product_title');
  }
  get product_price(){
    return this.createProductForm.get('product_price');
  }
  get product_details(){
    return this.createProductForm.get('product_details');
  }

  get product_quantity(){
    return this.createProductForm.get('product_quantity');
  }
  get product_category(){
    return this.createProductForm.get('product_category');
  }
  get product_image(){
    return this.createProductForm.get('product_image');
  }

  getCategories(){

  this.categoryy.getCategories().subscribe(
    (res:GetCategories)=>{
      this.category = res;

    }
  )

  }

  onSubmit(){

     this.craeteproduct.craete_new_product(this.createProductForm.value ).subscribe((res:productResponse)=>{
   
       this.snack_bar.open(res.message,"OK",{
         duration:2000,
       });

      if(res.code == 1){
        this.createProductForm.reset();  
      }
  
     })
     
  
   
  }

}
