import { CheckLoginUserService, loginResponsce } from './../../services/check-login-user.service';
import { MyOrderServiceService, MyOrdersResponse } from './../../services/my-order-service.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-my-orders',
  templateUrl: './my-orders.component.html',
  styleUrls: ['./my-orders.component.css']
})
export class MyOrdersComponent implements OnInit {

  constructor(private myOrders :MyOrderServiceService,
  private isLoggedIn :CheckLoginUserService) { }

  MyOrdersData:MyOrdersResponse;

  ngOnInit() {

    this.isLoggedIn.isLoggedIn().subscribe(
      (res:loginResponsce)=>{
   console.log(res.id);
        this.myOrders.getMyOrdersItems(res.id).subscribe(
          (res2:MyOrdersResponse)=>{
             this.MyOrdersData = res2
            
          }
        )
  })

}
}