import { CookieService } from 'ngx-cookie-service';
import { LoginUserService, loginResponse } from '../../services/login-user.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CheckLoginUserService, loginResponsce } from '../../services/check-login-user.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private login :LoginUserService,
               private cookieService : CookieService,
              private route : Router,
            private snack_bar:MatSnackBar ) { }

  //hide variable
  hide:boolean = true;
 
  //message from login api
  _message: loginResponse ;

  //initialize reactive form form for login
  loginForm = new FormGroup({
    email : new FormControl('',[Validators.required, Validators.email]),
    password : new FormControl('',[Validators.minLength(6),Validators.required]),
    login_token : new FormControl(this.cookieService.get('SSID'))
                             });


    //getters for email and password
    get email(){
      return this.loginForm.get('email');
    }
    get password(){
      return this.loginForm.get('password');
    }

    //implement onSubmit method
    onSubmit(){
      console.log("form is submitting");
      this.login.loginUser(this.loginForm.value).
      subscribe((data : loginResponse) => { 
        this._message = data;
        this.snack_bar.open(data.message,"OK",{
        duration:2000,
      });}
    ).add(()=>{
      if(this._message.code == 0){
        
       this.cookieService.set('SSID',this._message.token);
       window.location.href="/"; 
  
    }else if(this._message.code == 4){
      window.location.href="/"; 
    }
  });
  
     
    }

  ngOnInit() {
 
      }

}