import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ControlCoroselService, getAllCorosel, AddCoroselResponse, deleteCoroselResponse } from './../services/control-corosel.service';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-control-corosel',
  templateUrl: './control-corosel.component.html',
  styleUrls: ['./control-corosel.component.css']
})
export class ControlCoroselComponent implements OnInit {

  constructor(private corosel :ControlCoroselService,
  private snack_bar:MatSnackBar) { }

  AllCorosels:getAllCorosel;

  ngOnInit() {

    this.corosel.getAllCorosels().subscribe(
      (res:getAllCorosel)=>{
        this.AllCorosels = res
      }
    )
  }

  image_url;
  //form group for corosel creating
  createCoroselForm = new FormGroup({
    corosel_url: new FormControl('',[Validators.required]),
    corosel_link : new FormControl('',[Validators.required]),
   
  });

  get corosel_url(){
    return this.createCoroselForm.get('corosel_url');
  }
  get  corosel_link (){
    return this.createCoroselForm.get('corosel_link');
  }

  //submit add corosel form
  onSubmit(){

     console.log(this.createCoroselForm.value)
    this.corosel.addCorosel(this.createCoroselForm.value).subscribe(
      (res:AddCoroselResponse)=>{
        this.snack_bar.open(res.message,"OK",{
          duration:2000
      })

      if(res.code == 1){
        this.createCoroselForm.reset();  

        this.corosel.getAllCorosels().subscribe(
          (res:getAllCorosel)=>{
            this.AllCorosels = res
          }
        )
      }
      }
    )
  }


//delete corosel
deleteCorosel(id){

  this.corosel.deleteCorosel(id).subscribe(
    (res:deleteCoroselResponse)=>{
      this.snack_bar.open(res.message,"OK",{
        duration:2000
    })

    if(res.code == 1){
      this.createCoroselForm.reset();  

      this.corosel.getAllCorosels().subscribe(
        (res:getAllCorosel)=>{
          this.AllCorosels = res
        }
      )
    }

    
    }

    
  )
}

}
