import { catchError } from 'rxjs/operators';
import { CookieService } from 'ngx-cookie-service';
import { ErrorHandlerService } from './../../services/error-handler.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LogoutAdminService {

  
  constructor(
    private cookieService: CookieService,
        private http: HttpClient,
        private errorHandler : ErrorHandlerService) { }

         //url of logout api
         url :string = "https://www.blogsar.com/easy_mall_apis/logOutAdmin.php";

             //get current cookie token of logged in Adimin
  token :string = this.cookieService.get('ASID');

   //now logout the current Admin
   data
   logoutAdmin(data: number){

    this.data = {

      "token" : this.token,
      "admin_id"    : data
    }


    return this.http.post<logoutAdminResponse>(this.url,this.data).pipe(
      catchError(this.errorHandler.handleError)
    );

  }

}

export interface logoutAdminResponse{
  id:number;
  expression:boolean;
 
}