import { CategoryServicesService, GetCategories } from './../services/category-services.service';
import { GetAllProductsService, getOneProductResponse, updateProductReponse } from './../services/get-all-products.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '../../../../node_modules/@angular/router';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';


@Component({
  selector: 'app-update-product',
  templateUrl: './update-product.component.html',
  styleUrls: ['./update-product.component.css']
})
export class UpdateProductComponent implements OnInit {

  constructor(private router:Router,
  private updateProduct:GetAllProductsService,
private route :ActivatedRoute,
private get_categories:CategoryServicesService,
private snack_bar:MatSnackBar) { }




productDetails:getOneProductResponse;


id;
ngOnInit() {

  this.id =this.route.snapshot.paramMap.get('product_id');

      this.updateProduct.getProduct(this.id).subscribe(
        (res:getOneProductResponse)=>{

         this.productDetails = res;
        }


      )
 
    
  


}

  updateProductForm = new FormGroup({
    product_title : new FormControl('',[Validators.required,Validators.minLength(6)]),
    product_image : new FormControl('',[Validators.required]),
    product_price : new FormControl('',[Validators.required]),
    product_details : new FormControl('',[Validators.required]),
    product_quantity : new FormControl('',[Validators.required]),
    product_category : new FormControl('',[Validators.required]),
    product_id : new FormControl(+this.route.snapshot.paramMap.get('product_id'))
  });

  
  

  get product_title(){
    return this.updateProductForm.get('product_title');
  }
  get product_price(){
    return this.updateProductForm.get('product_price');
  }
  get product_details(){
    return this.updateProductForm.get('product_details');
  }

  get product_quantity(){
    return this.updateProductForm.get('product_quantity');
  }
  get product_category(){
    return this.updateProductForm.get('product_category');
  }
  get product_image(){
    return this.updateProductForm.get('product_image');
  }

 

//get categories
category:GetCategories;
getCategories(){

   this.get_categories.getCategories().subscribe(
     (res:GetCategories)=>this.category = res
   )

}

//submit the update product form 
onSubmit(){
  console.log(this.updateProductForm.value);

  this.updateProduct.updateProduct(this.updateProductForm.value).subscribe(
    (res:updateProductReponse)=>{
      this.snack_bar.open(res.message,"OK",{
        duration:2000,
      });
    }
  ).add((res:updateProductReponse)=>{

    if(res.code == 1){
      this.router.navigate(['/']);

    }
  }
   
  )
}



}
