import { LogoutAdminService } from './../services/logout-admin.service';
import { CookieService } from 'ngx-cookie-service';
import { AdminLoginResponse } from './../services/admin-login.service';
import { CheckLoginAdminService, adminLoginResponsce } from './../services/check-login-admin.service';
import { DeleteCategoryComponent } from './../delete-category/delete-category.component';
import { CreateCategoryModelComponent } from './../create-category-model/create-category-model.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '../../../../node_modules/@angular/material/dialog';

@Component({
  selector: 'app-admin-home',
  templateUrl: './admin-home.component.html',
  styleUrls: ['./admin-home.component.css']
})
export class AdminHomeComponent implements OnInit {

  constructor(private dialog:MatDialog,
  private isAdminLoggedIn :  CheckLoginAdminService,
  private cookieService: CookieService,
private logout :LogoutAdminService) { }

  adminName:string;
  adminLoginId:number;
  ngOnInit() {
    this.isAdminLoggedIn.isadminLoggedIn().subscribe(
      (res:adminLoginResponsce)=>{

        this.adminName = res.name,
        this.adminLoginId = res.id
      }
    )
  }

   
  openCreateCategoryDialog(){


    let dialogRef = this.dialog.open(CreateCategoryModelComponent,{
      width:'600px',
      height:'auto'
      
    })
  }

  openDeleteCategoryDialog(){

    let dialogReff = this.dialog.open(DeleteCategoryComponent,{
      width:'600px',
      height:'auto'
      
    })

  }


    //logout method for doing looged out current user
     logoutAdmin(data:number){


      this.logout.logoutAdmin(data).subscribe(res=> {
          
          if(res.expression){
     
          this.cookieService.delete('ASID');
         
          window.location.href="/"
  
        } } )

      }
    

}
