import { LoadingStatusService } from './../../services/loading-status.service';
import { getProductsForHomepageResponse, GetProductsForHomepageService } from './../../services/get-products-for-homepage.service';
import { getAllCorosel, ControlCoroselService } from './../../adminComponents/services/control-corosel.service';
import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  constructor(private corosel:ControlCoroselService,
  private get_product:GetProductsForHomepageService ,
private loading :LoadingStatusService) { }
  allCoroselImages:getAllCorosel;
  getAllProducts:getProductsForHomepageResponse;

 
  ngOnInit() {

    this.corosel.getAllCorosels().subscribe(
      (res:getAllCorosel)=>{
        this.allCoroselImages = res;
      }
    )


    this.get_product.getProductsForHomepage().subscribe(
      (res:getProductsForHomepageResponse)=>{
        this.getAllProducts=res
        
      }
    )
  }

  



}
