import { map, tap } from 'rxjs/operators';
import { CheckLoginUserService, loginResponsce } from '../services/check-login-user.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router, CanActivateChild } from '@angular/router';
import { Observable, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SignupGuardService implements CanActivate,CanActivateChild{

  constructor(private route :Router,
              private isLoggedIn : CheckLoginUserService) { }

       //implement canActivate guard
  canActivate(next:ActivatedRouteSnapshot,
              state:RouterStateSnapshot){

          let url:string = state.url;

          return this.isLoggedIn.isLoggedIn().pipe(
            map(res => {
              if(res.expression){
                return true;
              }else{
                this.route.navigate(['/login'])
        }
      })
    )
      
   

      
  }

  canActivateChild(next:ActivatedRouteSnapshot,
    state:RouterStateSnapshot){

let url:string = state.url;

return this.isLoggedIn.isLoggedIn().pipe(
  map(res => {
    if(res.expression){
      return true;
    }else{
      this.route.navigate(['/login'])
}
})
)




}

  
}

