import { ErrorHandlerService } from './../../services/error-handler.service';
import { catchError } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ControlCoroselService {

  constructor(private http:HttpClient,
  private errorHandler:ErrorHandlerService) { }


  getCoroselsUrl:string = "https://www.blogsar.com/easy_mall_apis/getCoroselImages.php";
  addCoroselsUrl:string = "https://www.blogsar.com/easy_mall_apis/addCorosel.php";
  deleteCoroselsUrl:string = "https://www.blogsar.com/easy_mall_apis/deleteCorosel.php";


  //get all corosels
  
  getAllCorosels():Observable<getAllCorosel>{

    return this.http.get<getAllCorosel>(this.getCoroselsUrl).pipe(
      catchError(this.errorHandler.handleError)
      )
    
  }

  ///add new corosel
  addCorosel(data:AddCorosel):Observable<AddCoroselResponse>{

    return this.http.post<AddCoroselResponse>(this.addCoroselsUrl,data).pipe(
      catchError(this.errorHandler.handleError)
    )

  }

  //delete Corrosel
  id;
  deleteCorosel(data):Observable<deleteCoroselResponse>{

    this.id={
      "corosel_id":data
    }

    return this.http.post<deleteCoroselResponse>(this.deleteCoroselsUrl,this.id).pipe(
      catchError(this.errorHandler.handleError)
    )

  }
}

export interface getAllCorosel{
  id:number;
  image_url:string;
  link:string;

}

export interface AddCorosel{
  corosel_url:string;
  corosel_link:string;
}

export interface AddCoroselResponse{
  message:string;
  code:number;
}

export interface deleteCoroselResponse{
  message:string;
  code:number;
}

