import { CheckLoginAdminService } from '../adminComponents/services/check-login-admin.service';
import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { map } from '../../../node_modules/rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AdminGuardService implements CanActivate{

  constructor(private route :Router,
    private isAdminLoggedIn : CheckLoginAdminService) { }

       //implement canActivate guard
       canActivate(next:ActivatedRouteSnapshot,
        state:RouterStateSnapshot){
   
  
          return this.isAdminLoggedIn.isadminLoggedIn().pipe(
            map(res => {
              if(res.expression){
                return true;
              }else{
                this.route.navigate(['/admin-login'])
        }
      })
    )
 

}


}
