import { catchError } from 'rxjs/operators';
import { Observable } from 'rxjs/internal/Observable';
import { ErrorHandlerService } from './../../services/error-handler.service';
import { CookieService } from 'ngx-cookie-service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CheckLoginAdminService {

 
  constructor(private http: HttpClient,
    private cookieService: CookieService,
     private errorHandler : ErrorHandlerService) { }

     url :string = "https://www.blogsar.com/easy_mall_apis/checkAdminLogin.php";

     data = {

      "token" : this.cookieService.get('ASID')
  
    }

    isadminLoggedIn(): Observable<adminLoginResponsce>{

      return this.http.post<adminLoginResponsce>(this.url,this.data).pipe(
        catchError(this.errorHandler.handleError)
      );
    }
}


export interface adminLoginResponsce{
  id: number;
  expression : boolean;
  name:string;
}
