import { updateProductReponse } from './get-all-products.service';
import { catchError } from 'rxjs/operators';
import { Observable } from 'rxjs/internal/Observable';
import { ErrorHandlerService } from './../../services/error-handler.service';
import { HttpClient } from '@angular/common/http';
import { Injectable, AfterViewInit } from '@angular/core';
import { typeWithParameters } from '../../../../node_modules/@angular/compiler/src/render3/util';

@Injectable({
  providedIn: 'root'
})
export class GetAllProductsService implements AfterViewInit {
  ngAfterViewInit(): void {

  }



  constructor(private http:HttpClient,
    private errorHandler : ErrorHandlerService ) { }

    url:string="https://www.blogsar.com/easy_mall_apis/getAllProducts.php"
    deleteurl:string="https://www.blogsar.com/easy_mall_apis/deleteProduct.php"
    getProductUrl:string="https://www.blogsar.com/easy_mall_apis/getProduct.php"
    updateProductUrl:string="https://www.blogsar.com/easy_mall_apis/updateProduct.php"

//return all products for admin dashboard page
 getAllProducts():Observable<allProducts[]>{

  return this.http.get<allProducts[]>(this.url).pipe(
  catchError(this.errorHandler.handleError));

 }

//get only specific product by given id for updating product purpose
id;
 getProduct(data):Observable<getOneProductResponse>{

   this.id = {
     "product_id":data,
   }
   return this.http.post<getOneProductResponse>(this.getProductUrl,this.id).pipe(
     catchError(this.errorHandler.handleError)
   )
 }


 //update_product
 updateProduct(data:updateProduct):Observable<updateProductReponse>{

  return this.http.post<updateProductReponse>(this.updateProductUrl,data).pipe(
    catchError(this.errorHandler.handleError)
  )

 }



product_id;

//delete produdct of given id
 deleteProduct(id:number):Observable<deleteResponse>{

  this.product_id = {

    "product_id"    : id
  }
  

  return this.http.post<deleteResponse>(this.deleteurl,this.product_id).pipe(
    catchError(this.errorHandler.handleError)
  )

 }



}


export interface allProducts{

  product_id:number;
  product_title:string;
  product_image:string;
  product_price:number;
  product_details:string;
  product_ratings:number;
  product_quantity:number;
  product_sales:number;
  product_badge:string;
  product_category:string;
  

}


export interface deleteResponse{
  message:string;
  code:number;
}

export interface getOneProductResponse{
  product_id:number;
  product_title:string;
  product_image:string;
  product_price:number;
  product_details:string;
  product_ratings:number;
  product_quantity:number;
  product_sales:number;
  product_badge:string;
  product_category:string;
}


export interface updateProduct{

  product_id:number;
  product_title:string;
  product_image:string;
  product_price:number;
  product_details:string;
  
  product_quantity:number;

  product_category:string;
  
}


export interface updateProductReponse{
  message:string;
  code:number;
}