import { catchError } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { ErrorHandlerService } from './error-handler.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class GetProductsForHomepageService {

  constructor(private http:HttpClient,
  private errorHandler:ErrorHandlerService) { }

  url:string="https://www.blogsar.com/easy_mall_apis/GetProductsForHomepage.php";

  //return all products for home page
  getProductsForHomepage():Observable<getProductsForHomepageResponse>{
    return this.http.get<getProductsForHomepageResponse>(this.url).pipe(
      catchError(this.errorHandler.handleError)
    )
  }
}


export interface getProductsForHomepageResponse{
  product_id:number;
  product_title:string;
  product_image:string;
  product_price:number;
  product_details:string;
  product_ratings:number;
  product_quantity:number;
  product_sales:number;
  product_badge:string;
  product_category:string;
}