import { Observable } from 'rxjs/internal/Observable';
import { catchError } from 'rxjs/operators';
import { ErrorHandlerService } from './error-handler.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CategoryServiceService {

  constructor(private http:HttpClient,
  private ErrorHandler:ErrorHandlerService) { }


  getCategoryProductUrl:string = "https://www.blogsar.com/easy_mall_apis/ItemByCategory.php";

  //method for getting category by products
data;
  getItemByyCategory(data):Observable<categoryItemResponse>{

    this.data = {
      'category_id':data
    }

    return this.http.post<categoryItemResponse>(this.getCategoryProductUrl,this.data).pipe(
      catchError(this.ErrorHandler.handleError)
    )
  }
}

export interface categoryItemResponse{
  product_id:number;
  product_title:string;
  product_image:string;
  product_price:number;
  product_details:string;
  product_ratings:number;
  product_quantity:number;
  product_sales:number;
  product_badge:string;
  product_category:string;


}
