import { getResponse } from '../services/sign-up.service';
import { ErrorHandlerService } from '../services/error-handler.service';
import { HttpClient } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import { CheckLoginUserService, loginResponsce } from '../services/check-login-user.service';
import { Injectable } from '@angular/core';

import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class LogoutUserService {
  dataa: { "token": string; "id": number; };

  constructor(
    private cookieService: CookieService,
        private http: HttpClient,
        private errorHandler : ErrorHandlerService) { }

        //url of logout api
        url :string = "https://www.blogsar.com/easy_mall_apis/logoutUser.php";

     //get current cookie token of logged in user
  token :string = this.cookieService.get('SSID');

  //now logout the current user
  logoutUser(data: number){

    this.dataa = {

      "token" : this.token,
      "id"    : data
    }

    
    return this.http.post<loginResponsce>(this.url,this.dataa).pipe(
      catchError(this.errorHandler.handleError)
    );
  }
 


 

}
