import { Observable } from 'rxjs/internal/Observable';
import { catchError } from 'rxjs/operators';
import { ErrorHandlerService } from './error-handler.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class MyOrderServiceService {


  constructor(private http:HttpClient
  ,private errorHandler : ErrorHandlerService) { }


  getMyOrdersUrl :string = "https://www.blogsar.com/easy_mall_apis/getMyOrders.php";

  //method for getting my orders ites
  user_id;
  getMyOrdersItems(data):Observable<MyOrdersResponse>{

     this.user_id = {
       'user_id':data
     }


     return this.http.post<MyOrdersResponse>(this.getMyOrdersUrl,this.user_id).pipe(
       catchError(this.errorHandler.handleError)
     )
  }
}


export interface MyOrdersResponse{
  order_id:number;
  user_id:number;
  product_id:number;
  quantity:number;
  price:number;
  address:string;
  title:string;
  status:string;
  Ordering_time:string;
  product_image:string;
}
