import { CategoryServicesService, GetCategories, deleteCategoryResponse } from './../services/category-services.service';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-delete-category',
  templateUrl: './delete-category.component.html',
  styleUrls: ['./delete-category.component.css']
})
export class DeleteCategoryComponent implements OnInit {

  constructor(private category:CategoryServicesService,
  private snack_bar:MatSnackBar) { }

  categories:GetCategories;

  ngOnInit() {

     this.category.getCategories().subscribe(
       (res:GetCategories)=>{

        this.categories = res;

       }
     )

  }

  deleteCategory(id:number){

     this.category.deleteCategory(id).subscribe(
       (res:deleteCategoryResponse)=>{
        this.snack_bar.open(res.message,"OK",{
          duration:2000,
        });


        this.category.getCategories().subscribe(
          (res:GetCategories)=>{
   
           this.categories = res;
   
          }
        )
       }
     )
  }

}
