import { LoadingStatusService } from './../../services/loading-status.service';
import { CategoryServicesService, GetCategories } from './../../adminComponents/services/category-services.service';
import { HomePageComponent } from './../home-page/home-page.component';
import { CookieService } from 'ngx-cookie-service';
import { LogoutUserService } from '../../logoutUser/logout-user.service';
import { CheckLoginUserService, loginResponsce } from '../../services/check-login-user.service';
import { Component, OnInit} from '@angular/core';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {


   
  

  constructor(private loginUser :CheckLoginUserService,
              private logout: LogoutUserService,
              private cookieService: CookieService,
            private categories:CategoryServicesService,
          private loaidng:LoadingStatusService) { }
   
  loaded:boolean;
  loginId :number;
  loggedIn:boolean;
  userName:string;
  AllCategories:GetCategories;

  ngOnInit() {
    this.loginUser.isLoggedIn().subscribe(
       (res: loginResponsce)=> {
         this.loginId = res.id ,
         this.loggedIn = res.expression,
         this.userName = res.name
        })

        //getall  categoris
        this.categories.getCategories().subscribe(
          (res:GetCategories)=>{
            this.AllCategories = res
          }
        )

        //get loading stattus for progress bar
          this.loaidng.isLoaded().subscribe(
            res=>this.loaded = res
          )
            ;

           
    }

  
   
  


    //logout method for doing looged out current user
    logoutUserr(data:number){


    this.logout.logoutUser(data).subscribe(res=> {
        
        if(res.expression){
   
        this.cookieService.delete('SSID');
        window.location.href="/";
        

      } } )
    
  

    }


}