import { UpdateProfileInfoResponse } from './../../services/user-profile-info.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ProfileInfoResponse, UserProfileInfoService } from '../../services/user-profile-info.service';
import { ActivatedRoute, Router } from '../../../../node_modules/@angular/router';
import { MatSnackBar } from '../../../../node_modules/@angular/material/snack-bar';

@Component({
  selector: 'app-my-account',
  templateUrl: './my-account.component.html',
  styleUrls: ['./my-account.component.css']
})
export class MyAccountComponent implements OnInit {

  constructor(private user_profile:UserProfileInfoService,
  private route :ActivatedRoute,
private router:Router,
private snack_bar :MatSnackBar) { }

UserProfileData:ProfileInfoResponse;

ngOnInit() {

  this.userInfoUpdateForm.disable();
    let id = this.route.snapshot.paramMap.get('id');
   

this.user_profile.getProfileInfo(id).subscribe(

  (res:ProfileInfoResponse)=>{
    if(+res.mobile_number == 0){
      this.UserProfileData = res
       this.router.navigate(['/'])
    }else{
      this.UserProfileData = res

      this.userInfoUpdateForm.patchValue({
        Name:this.UserProfileData[0].Name,
        email:this.UserProfileData[0].email,
        mobile_number:this.UserProfileData[0].mobile_number,
        address1:this.UserProfileData[0].address1,
        address2:this.UserProfileData[0].address2,
      })
    }
   
  }
)

  }



  userInfoUpdateForm = new FormGroup({
    Name : new FormControl('',[Validators.required]),
    email : new FormControl('',[Validators.required, Validators.email]),
    mobile_number : new FormControl('',[Validators.required,Validators.minLength(10)]),
    address1 : new FormControl('',[Validators.required,Validators.minLength(10)]),
    address2 : new FormControl('',[Validators.required,Validators.minLength(10)]),
    id : new FormControl(this.route.snapshot.paramMap.get('id')),
  });

  isFormDisabled:boolean = false;

 

  get Name(){
    return this.userInfoUpdateForm.get('Name');
  }
  get email(){
    return this.userInfoUpdateForm.get('email');
  }
  get mobile_number(){
    return this.userInfoUpdateForm.get('mobile_numbe');
  }

  get address1(){
    return this.userInfoUpdateForm.get('address1');
  }


  get address2(){
    return this.userInfoUpdateForm.get('address2');
  }


  enableForm(){
    this.userInfoUpdateForm.enable();
  }



  //update the user info
  onSubmit(){
    this.userInfoUpdateForm.disable();
console.log(this.userInfoUpdateForm.value);
this.user_profile.updateProfileInfo(this.userInfoUpdateForm.value).subscribe(
  (res:UpdateProfileInfoResponse)=>{
    this.snack_bar.open(res.message,"OK",{
      duration:2000,
    });

    this.userInfoUpdateForm.disable();

    this.user_profile.getProfileInfo(this.route.snapshot.paramMap.get('id')).subscribe(

      (res:ProfileInfoResponse)=>{
        if(+res.mobile_number == 0){
          this.UserProfileData = res
           this.router.navigate(['/'])
        }else{
          this.UserProfileData = res
    
          this.userInfoUpdateForm.patchValue({
            Name:this.UserProfileData[0].Name,
            email:this.UserProfileData[0].email,
            mobile_number:this.UserProfileData[0].mobile_number,
            address1:this.UserProfileData[0].address1,
            address2:this.UserProfileData[0].address2,
          })
        }
       
      }
    )
  }
)
  }

}
