import { catchError } from 'rxjs/operators';
import { getResponse } from '../../services/sign-up.service';
import { ErrorHandlerService } from '../../services/error-handler.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';

@Injectable({
  providedIn: 'root'
})
export class AdminSignupServiceService {

  constructor(private http: HttpClient,
    private errorHandler : ErrorHandlerService) { }


      //api url
  signUpUrl :string = "https://www.blogsar.com/easy_mall_apis/adminSignUp.php";

  //headers currently not in use
  httpOptions = {
 headers : new HttpHeaders({
'content-type' : 'application/json',
                          })
                }



       //method for signing up
  adminSignup(admin:Admin) :Observable<getResponse>{

    return this.http.post<getResponse>(this.signUpUrl,admin,this.httpOptions).pipe(
      catchError(this.errorHandler.handleError)
    );
  }


}


  //interface for user data that we are sending to server
  export interface Admin{
    name :string;
    email:string;
    password:string;
      }